@import "../../Common/commonStyle.less";

.homePage-ipad {
    p {
        margin: 0;
        padding: 0;
    }

    position: relative;
    width: 100%;
    background-color: rgba(19, 20, 44, 1);

    // 头部样式
    header {
        position: relative;
        width: 100vw;
        height: 100*0.537vw;
        background:
            url('../../Assets/header/ipad_01@2x.png') 0 0 no-repeat,
            url('../../Assets/header/ipad_02@2x.png') 0 18.9vw no-repeat,
            url('../../Assets/header/ipad_03@2x.png') 0 37.8vw no-repeat;
        background-size: 100%;
        // margin-bottom: 100px;

        hgroup {
            position: absolute;
            width: 100%;
            top: 100*0.316vw;
            left: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            color: #fff;
            text-align: center;
            z-index: 3;

            .banner-p1 {
                font-size: 3.28rem;
                font-family: @PingFang-SC-Heavy-fontFamily;
                font-weight: 800;
                color: rgba(255, 255, 255, 1);
                line-height: 4.57rem;
                letter-spacing: 1px;
                text-shadow: 0px 0px 1px rgba(100, 223, 222, 0.6);
                // animation: bannerP1 5s;
            }

            .banner-p2 {
                font-size: 1.85rem;
                font-family: @PingFang-SC-Bold-fontFamily;
                font-weight: bold;
                color: rgba(100, 223, 222, 1);
                line-height: 2.71rem;
                text-shadow: 0px 2px 14px rgba(100, 223, 222, 0.3);
            }
        }
    }

    main {
        background-color: rgba(19, 20, 44, 1);

        .explanation-box {
            display: flex;
            flex-direction: row;
            justify-content: space-around;
        }

        li {
            width: 22.19vw;

            .explanation-head {
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-items: center;
                margin-bottom: 1.57rem;
                position: relative;

                .explanation-head-img {
                    width: 6.6vw;
                    height: 8.62vw;
                    line-height: 8.62vw;

                    img {
                        width: 100%;
                    }

                }

                .explanation-head-adorn {
                    position: absolute;
                    width: 12vw;
                    animation: explanation_head_adorn 4s linear infinite alternate;

                }

                .explanation-head-title {
                    min-width: 120px;
                    position: relative;
                    display: inline-block;
                    text-align: center;
                    margin-bottom: 4%;
                    margin-top: 10%;

                    img {
                        position: absolute;
                        left: 0;
                        right: 0;
                        margin: 0 auto;
                        width: 100px;
                        top: 12%;
                    }

                    span {
                        display: inline-block;
                        text-align: center;
                        font-size: 1.57rem;
                        font-family: @PingFang-SC-Heavy-fontFamily;
                        font-weight: 800;
                        color: rgba(100, 223, 222, 1);
                        line-height: 2.14rem;
                        letter-spacing: 1px;
                        text-shadow: 0px 2px 14px rgba(100, 223, 222, 0.6);
                    }
                }

            }

            p {
                display: inline-block;
                width: 160px;
                font-family: @PingFangSC-Light-fontFamily;
                font-weight: 300;
                color: rgba(255, 255, 255, 1);
                line-height: 20px;

                font-size: 1rem;
                line-height: 22px;
            }
        }

        // 联系我们
        .contact-us-box {
            position: relative;
            margin-top: 16vw;
            margin-bottom: 4vw;

            .contact-us-bg {
                position: relative;
                height: 44vw;

                img {
                    position: absolute;
                }


                img:nth-child(1) {
                    width: 84vw;
                    left: 12.1vw;
                }

                img:nth-child(2) {
                    width: 13.2vw;
                    right: 26.5%;
                    top: -21%;
                    animation: contactLogo-ipad 4s linear infinite alternate;

                }


                img:nth-child(3) {
                    width: 13vw;
                    right: 26.5%;
                    top: -3%;
                    animation: contactAdorn 5s linear infinite;

                }

                img:nth-child(4) {
                    width: 10vw;
                    left: 17.5%;
                    top: 35%;
                    opacity: .6;
                    animation: contactAdorn2 5s linear infinite;
                }

                img:nth-child(5) {
                    width: 10vw;
                    left: 17.5%;
                    top: 14%;
                    opacity: .6;
                    animation: contactAdorn3 5s linear infinite;

                }
            }

            .contact-us-txt {
                position: absolute;
                left: 8.5%;
                top: -3vw;

                p {
                    margin-left: .6rem;
                    font-size: 24px;
                    font-family: @PingFangSC-Regular-fontFamily;
                    font-weight: 400;
                    color: rgba(255, 255, 255, 1);
                    line-height: 33px;
                    text-shadow: 0px 2px 14px rgba(0, 214, 255, 0.5);
                    margin-bottom: 1rem;
                }

                .uploadBtn-ipad {
                    position: relative;
                    display: flex;
                    flex-direction: row;
                    justify-content: flex-start;
                    align-items: center;
                    width: 28.9vw;
                    background: none;
                    border: none;
                    outline: none;
                    padding: 0;
                    margin: 0;
                    margin-bottom: 20px;
                    border-radius:40px;
                    box-shadow:0px 2px 10px 0px rgba(100,223,222,0.1);
                    cursor: pointer;

                    .btn-bg {
                        width: 100%;
                    }

                    span {
                        font-size: 24px;
                        font-family: @PingFangSC-Regular-fontFamily;
                        font-weight: 400;
                        color: rgba(255, 255, 255, 1);
                        line-height: 2.42rem;
                        text-shadow: 0px 2px 14px rgba(0, 214, 255, 0.5);
                    }

                    .btn-icon {
                        position: absolute;
                        width: 4.5vw;
                        margin-left: 25px;
                        margin-right: 10px;
                    }

                    .btn-txt {
                        position: absolute;
                        left: 35%;

                    }
                    .google-txt {

                        left: 32%;
                    }


                    .googlePlay {
                        margin-right: 7px;
                    }
                }

            }
        }

    }
}


@keyframes explanation_head_adorn {
    0% {
        top: -10%;
        opacity: 0;
    }

    50% {
        top: 0%;
        opacity: 1;
    }

    100% {
        top: 10%;
        opacity: 0;
    }
}

@keyframes contactLogo-ipad {
    0% {
        top: -21%;
        opacity: 0;
    }

    50% {
        top: -24%;
        opacity: 1;
    }

    100% {
        top: -27%;
        opacity: 0;
    }
}

@keyframes contactAdorn2 {
    0% {
        top: 35%;
        opacity: 0;
    }

    50% {
        top: 31%;
        opacity: .6;
    }

    100% {
        top: 27%;
        opacity: 0;
    }
}

@keyframes contactAdorn3 {
    0% {
        top: 14%;
        opacity: 0;
    }

    50% {
        top: 10%;
        opacity: .6;
    }

    100% {
        top: 6%;
        opacity: 0;
    }
}